.products {
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;

  .productResults {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
  }
}

.product {
  width: 20%;
  margin: 0 ;
  padding: 0;

  .thumb {
    display: block;
    width: 100%;
    

    img {
      display: inline;
      width: 80%;
      height:60%;
      margin: 0;
    }
  }

  .details {
    display: block;
    width: 60%;
    padding: 0;
    margin: 0 auto;

    ul, ul li {
      padding: 0;
      margin: 0;
    }

    ul {
      li {
        display: block;
        width: 80%;
        list-style-type: none;
        text-align: left;
        margin: 0 0 .5rem;

        a {
          color: black;
        }

        .name {
          font-size: 2.2rem;
          line-height: 1.2;
          font-weight: 400;
        }

        .price {
          font-size: 1.6rem;
          line-height: 1;
          font-weight: 400;
        }

        .addToCart {
          margin: 2rem 0 0 0;
        }
      }
    }

  }
}